"use client"
import React, { useEffect, useState } from 'react'
import { getUserById } from '../services/users';
import userStore from '../stores/userStore';

interface CardViewProps {
    userId: number;
}

function CardView({ userId }: CardViewProps) {
    const [user, setUser] = useState<UserModel | null>(null);
    const [isEditing, setIsEditing] = useState(false); // מציין אם אנחנו במצב עריכה
    const [updatedUser, setUpdatedUser] = useState<UserModel | null>(null); // מייצג את המידע החדש של המשתמש

    const deleteUser = userStore((state) => state.deleteUser);
    const updateUser = userStore((state) => state.updateUser);

    useEffect(() => {
        const fetchUser = async () => {
            try {
                const userData = await getUserById(userId);
                setUser(userData);
                setUpdatedUser(userData); // מעדכנים את ה-user לשם עריכה
            } catch (error) {
                console.error("Error fetching user:", error);
            }
        };
        fetchUser();
    }, [userId]);

    const handleDelete = () => {
        if (user) {
            deleteUser(user.id); // מחיקת המשתמש מהחנות
            alert('User deleted');
        }
    };

    const handleUpdate = () => {
        if (updatedUser) {
            updateUser(updatedUser); // עדכון המשתמש בחנות
            setIsEditing(false); // סיום עריכה
            alert('User updated');
        }
    };

    const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const { name, value } = e.target;
        if (updatedUser) {
            setUpdatedUser((prevUser) => ({
                ...prevUser,
                [name]: value
            }));
        }
    };

    return (
        <div className="w-1/3 mx-auto my-4 p-4 bg-white rounded-lg shadow-md">
            <img
                src="https://via.placeholder.com/80"
                alt="Avatar"
                className="w-20 h-20 rounded-full mx-auto mb-2"
            />
            <div className="text-center">
                {!isEditing ? (
                    <>
                        <h2 className="text-xl font-bold">{user?.firstName} {user?.lastName}</h2>
                        <p className="text-gray-600">Email: {user?.email}</p>
                        <p className="text-gray-600">Phone: (123) 456-7890</p>
                        <div className="mt-4">
                            <button
                                onClick={() => setIsEditing(true)} // מגדירים את המצב לעריכה
                                className="bg-yellow-500 text-white py-2 px-4 rounded mr-2"
                            >
                                Update
                            </button>
                            <button
                                onClick={handleDelete}
                                className="bg-red-500 text-white py-2 px-4 rounded"
                            >
                                Delete
                            </button>
                        </div>
                    </>
                ) : (
                    <div>
                        <h2 className="text-xl font-bold">Edit User</h2>
                        <div className="mt-4">
                            <label className="block text-sm font-medium text-gray-700">First Name</label>
                            <input
                                type="text"
                                name="firstName"
                                value={updatedUser?.firstName || ''}
                                onChange={handleInputChange}
                                className="w-full px-3 py-2 border border-gray-300 rounded-md"
                            />
                        </div>
                        <div className="mt-4">
                            <label className="block text-sm font-medium text-gray-700">Last Name</label>
                            <input
                                type="text"
                                name="lastName"
                                value={updatedUser?.lastName || ''}
                                onChange={handleInputChange}
                                className="w-full px-3 py-2 border border-gray-300 rounded-md"
                            />
                        </div>
                        <div className="mt-4">
                            <label className="block text-sm font-medium text-gray-700">Email</label>
                            <input
                                type="email"
                                name="email"
                                value={updatedUser?.email || ''}
                                onChange={handleInputChange}
                                className="w-full px-3 py-2 border border-gray-300 rounded-md"
                            />
                        </div>
                        <div className="mt-4">
                            <button
                                onClick={handleUpdate} // שומר את העדכון
                                className="bg-green-500 text-white py-2 px-4 rounded mr-2"
                            >
                                Save
                            </button>
                            <button
                                onClick={() => setIsEditing(false)} // סוגר את מצב העריכה
                                className="bg-gray-500 text-white py-2 px-4 rounded"
                            >
                                Cancel
                            </button>
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
}

export default CardView;
